import React, { useState } from "react";
import s from './Pagination.module.css';
import cn from 'classnames';
import prevBtn from '../../../assets/images/prev.svg';
import nextBtn from '../../../assets/images/next.svg';

const Pagination = ({ totalItemsCount, pageSize, onPageChanged, currentPage, portionSize = 20 }) => {

  let pagesCount = Math.ceil(totalItemsCount / pageSize);
  let pages = [];

  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  let portionCount = Math.ceil(pagesCount / portionSize); 
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1; 
  let rightPortionPageNumber = portionNumber * portionSize; 

  return (<div className={s.pagination}>
    {<button className={s.prevBtn} disabled={portionNumber <= 1} onClick={() => setPortionNumber(portionNumber - 1)}><img src={prevBtn} alt='' /></button>}

    {pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber).map(p => <span className={cn({ [s.selectedPage]: currentPage === p }, s.pageNumber)} key={p} onClick={(e) => onPageChanged(p)}>{p}</span>)}

    {<button className={s.nextBtn} disabled={portionCount <= portionNumber} onClick={() => setPortionNumber(portionNumber + 1)}><img src={nextBtn} alt='' /></button>}
  </div>
  )
}

export default Pagination;